import React from 'react';
import {BlogEntry,BlogList} from "../index"

const aleesaBlog = () => 
<BlogList name="aleesa">
    <BlogEntry title="Prepping Minis" date="12019-10-29 HE">
    <p>
    This was a day of prep-work.  Liz and I are working hard to get attention and raise funds through selling painted minis and offering commissions. 
    </p> 
    <iframe frameBorder="0" scrolling="no" marginHeight="0" marginWidth="0" width="854" height="480" src="https://www.youtube.com/embed/yV9FH7HKdGE"></iframe>
    <p>
    I spent the first part of the day pulling out my Cryx army from Privateer Press’s Warmachine from their foam trays to photograph and fix up.  Several of the Satyxis Raiders’ whips had broken off, so the old glue needed to be cleaned off, some of the pin-holes needed to be re-drilled, then the whips could be glued back on.</p>
    <p>  
    I’d painted minis before, but the Cryx models were the first time I’d ever painted anything on an army scale, nearly 35 total models, including the gigantic Kraken.  I think my painting technique has improved a fair bit since i painted them, but I’m still really proud of painting that many models up to that quality.
    </p>
    <p>
    I also created a Facebook marketplace link to facilitate selling them more easily, that you can check out here: <a href="https://www.facebook.com/marketplace/item/738127766654453/">Cryx Army for Sale on Facebook</a>
    </p>
    <img src="/assets/Blog12019-10-29-ZenithHighlighting.png" alt="Zenith Highlighted models" />
    <p>
    I also prepped some other models for painting. Two models for Liz where I primed them first in a mid-grey, then hit the top faces with white in a technique called Zenith Highlighting.  And three dog models that I’m doing as painting commissions that I primed white and then gave them a wash with a thinned down dark brown so that the pigment collects into the recesses and bottom of the model.  Both techniques are great for picking up a really basic sense of lighting and help details stand out.  They can also be combined together for a quick and easy value sketching base.  Next I’ll work on picking out specific shadow and highlight areas on the dogs that the wash didn’t, and then maybe get some color blocking.
    </p>
    <img src="/assets/Blog12019-10-29-WashShading.png" alt="Dark washed Dungeons and Doggies models" />
    <p>
    Besides all the miniature painting, I’ve also been working on some pixel art commissions for Freebird Games, for a fourth game in the To The Moon series.  I can’t share any of that work for the sake of spoilers, but it’s always fun to work on sprites.  I’ve got more of those to work on today as well.
    </p>   
    </BlogEntry>
</BlogList>

export default aleesaBlog;