import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const ActionStyles = styled.section`
margin: auto;
margin-top: 2rem;
background: linear-gradient(90deg, rgba(242,255,214,.2) 60%, rgba(242,255,214,0) 100%);
min-height: 100vh;

header{
    max-width: 854px;
    margin: 0 auto;
    padding-top: 1rem;
    iframe{
        max-width: 854px;
        width: 854px; /* For Chrome */
        max-height:480px;    
        margin: auto;
        box-shadow: 4px 4px 5px 0px #2e251e80;
    }
}

main{
    margin: auto;
    padding: 1rem;
}

footer{
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
        font-size: 1.1rem;
    }
}

.CallBack{
    flex: 1 0 100%;
    text-align: center;
}

a.External{
    flex: 1 0 40%;
    margin:20px;
    img {
        display: block;
        float: left;
    }
}


`

export const ThankYou = () =>
    <ActionStyles>
    <header>Thank you for your donation!</header>
        <main>
        <p>We're very excited to get back on our feet and keep pursuing local opportunities and this chance to do so is of immense help.</p>
        <p>Please feel free to ask for commissions for your donation at a later date as well, we are very happy to help those who have provided help</p>
        </main>
        <footer>
        <a className="External" href="https://discord.gg/kSQp9p3"><img src="/assets/Discord-Logo-White.png" alt="Discord" width="80px" height="80px"/>
        <h3>Now that you've donated please come join us on Discord to get the latest updates as we make our progress!</h3>
        </a>
        </footer>
    </ActionStyles>

export default ThankYou;