import React, {Fragment} from 'react';
import './App.css';
import {
  TransitionGroup,
  CSSTransition
} from "react-transition-group";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation} from "react-router-dom";

import PayPalButton from './Donate/paypal';
import {IllustrationCallout, IllustrationGallery} from './Illustration'
import {ModelsCallout,ModelsGallery} from './Models';
import {Transitions} from './Transitions';
import {CallToAction } from "./CallToAction";
import {PixelGallery, PixelCallout } from "./PixelArt";
import {MinisCallout, MinisGallery} from "./Miniatures";
import SalesPage from './ForSale';
import UtilityHeader from './Header';
import ScrollToTop from "./scrollToTop";
import {Landing} from './Landing';
import {BannerBlock} from './BaseBlock';
import ThankYou from './ThankYou';
import Liz from './Blog/liz';
import Aleesa from './Blog/aleesa';

function App() {
  return (
    <Transitions>
      <Router>
        <Switch>
          <Route path="*">
            <AnimatedTransitions />
          </Route>
        </Switch>
      </Router>
    </Transitions>
  );
}

export const CommissionsCallout = () => 
<BannerBlock className="Callout" path="commissions" name="Commission Team Tana" background="/assets/Background1.jpg" photos={[{name:"Illustrations", url:"/assets/ShadowboxThumb.png"}]}>
    <p>We create art in a number of formats and media, and are happy to share our quotes available on Discord!</p>
</BannerBlock>

export const SalesCallout = () => 
<BannerBlock className="Callout" path="forSale" name="Buy Art from Team Tana" background="/assets/Background1.jpg" photos={[{name:"Illustrations", url:"/assets/CryxThumb.png"}]}>
    <p>We have a number of existing pieces for sale, from illustrations to fully painted miniatures game armies, please come have a look and make any reasonable offer, even if it's lower than list price!</p>
</BannerBlock>



function AnimatedTransitions() {
  let location = useLocation();
  return (
    <Fragment>
    <ScrollToTop />
    <UtilityHeader location={location} />
    <PayPalButton />
    <TransitionGroup>
    <CSSTransition
      key={location.key}
      classNames="zoom-in"
      timeout={900}
    >
      <Switch location={location}>
          <Route exact path="/illustrations">
            <IllustrationGallery />
          </Route>
          <Route exact path="/callToAction">
            <CallToAction />
          </Route>
          <Route exact path="/models">
            <ModelsGallery />
          </Route>
          <Route exact path="/pixelart">
            <PixelGallery/>
          </Route>
          <Route exact path="/miniatures">
            <MinisGallery/>
          </Route>
          <Route exact path="/commissions">
          <div>
            <MinisCallout />
            <IllustrationCallout />
            <ModelsCallout />
            <PixelCallout />
          </div>
          </Route>
          <Route exact path="/forSale">
            <SalesPage />
          </Route>
          <Route exact path="/blog/liz">
            <Liz />
          </Route>
          <Route exact path="/blog/aleesa">
            <Aleesa />
          </Route>
          <Route path="/">
          <Landing>
            <SalesCallout />
            <CommissionsCallout/>
          </Landing>
          </Route>
      </Switch>
    </CSSTransition>
  </TransitionGroup>
  </Fragment>
  );
}

export default App;
