import React, {useState, Fragment} from 'react';
import {GalleryImg, GalleryItem} from './GalleryImage';
import {GalleryPage} from './GalleryPage';

const InnerGalleryMini = ({srcHint,alt,price,children,hide,toggleHide}) =>
    <GalleryItem hidden={hide} key={srcHint}>
        <h2>{alt}</h2>
        <div className='ImageBox'>
            <img onClick={toggleHide} src={`/assets/${srcHint}Thumb.png`} alt={alt} />
        </div>
        <div className="PriceContainer">
            <h3>${price}</h3>
        </div>
        <div className="Description">
            {children}
        </div>
        <MiniModal {...{toggleHide, hide, srcHint, alt}} />
    </GalleryItem>

const hintToVideo = {
    "Trolls": "Vkq40avpAn8",
    "PinkMercs": "jkmqNRGyq2M",
    "Cryx": "yV9FH7HKdGE"
}


const MiniModal = ({toggleHide, hide, srcHint, alt}) => 
<div onClick={toggleHide} 
className={`Modal 
${hide? "Modal-exit Modal-exit-active":"Modal-enter Modal-enter-active" }`}>
    {hide? <Fragment /> : <iframe width="560" height="315" src={`https://www.youtube.com/embed/${hintToVideo[srcHint]}`} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen="true"></iframe>}
</div>

const GalleryMini = (props) =>{
    let [hide,setHide] = useState(true);
    const handleHideClick = () => {
        setHide(!hide);
    }
    let combineProps = {...props,hide,toggleHide:handleHideClick}
    return <InnerGalleryMini {...combineProps}/>

}

const SalesGallery = () => 
<GalleryPage title="Miniatures">
    <GalleryMini srcHint="Trolls" alt="Trolls" price={"450 or best offer"}>
        <p>Click photo for video preview</p>
        <p>A collection of my Trollblood Hordes army, painted to high tabletop</p>
        <ul>
        <li>Madrak Ironhide 1</li>
        <li>Grissel Bloodsong</li>
        <li>Horluk Doomshaper 1</li>
        <li>Earthborn Dire Troll</li>
        <li>Dire Troll Mauler</li>
        <li>Dire Troll Blitzer</li>
        <li>Pyg Bushwackers Full Unit</li>
        <li>Pyre Troll</li>
        <li>Troll Axer</li>
        <li>Troll Impaler</li>
        <li>Krielstone Bearer Full Unit</li>
        <li>Trollblood Champions</li>
        </ul>
    </GalleryMini>
    <GalleryMini srcHint="Cryx" alt="Cryx" price="875 or best offer">
        <p>Has video preview!</p>
        <p>A high tabletop to mid-display quality army with Battlefoam to ship it in.</p>
        <p>Great force to start and stun a new player with!</p>
    </GalleryMini>
    <GalleryImg srcHint="Shadowbox" alt="Hail the King" price={"700 or best offer"} printUrl="https://www.redbubble.com/people/aleesatana/works/41050284-hail-the-king">
        <p>A young woman and her winged fox companion discover the remains of a long-dead king.  Painted in gouache as a multi-layer shadowbox.  11x14 inches.</p>
    </GalleryImg>

    <GalleryMini srcHint="PinkMercs" alt="Pink Mercenaries" price={"875 or best offer"}>
        <p>Has video preview!</p>
        <p>Part of my Breast Cancer Brawl army and I would love to see these be prize support for Breast Cancer Brawl or similar events.</p>
        <p>I will price them at $450 for any BCB event organizer</p>
    </GalleryMini>

    <GalleryImg srcHint="Vera" alt="Vera & Worms" price={"30 or best offer"}>
        <p>Vera and the Worms</p>
        <p>A miniature encounter seed in physical form that I enjoyed working on and is a good example of my more RPG mini roots.</p>
    </GalleryImg>
</GalleryPage>

export default SalesGallery;