import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import {BlogCallout} from './Blog'

const LandingStyles = styled.section`
margin: auto;
margin-top: 2rem;
background: linear-gradient(90deg, #f2ffd620 60%, #f2ffd600 100%);
min-height: 100vh;

h1 {
        color: #ff8a2b;
}

.landingHeader {
    h1 {
        margin-left: 2.33rem;
    }
}
.landingHeader h1:nth-child(even){
        margin-left:6rem;
        margin-top: -1rem;
        padding-top: 0;
}

.missionStatement {
        h2 {
            font-size: 2.33rem;
            color: #0d1111;
            text-align: center;
            margin: 0.1rem;
            padding: 0;
        }
        ul {
            max-width: 80%;
            margin: auto;
        }
        ul li{
	        font-family: delius, sans-serif;
        }
    }


.whosWho {
    max-width: 80vw;
    max-height:480px;
    margin: 0 auto;
    padding-top: 1rem;
    display: flex;
    justify-content: space-between;
    img{
        max-height: 240px;
        max-width: 240px;
    }


    div {
        h1,h2,h3 {
            text-align: center;
            margin: 0.1rem;
            padding: 0;
        }
        h1 {
            font-size: 3rem;
        }
        h2 {
            font-size: 2.33rem;
        }
        h3 {
            font-size: 1.66rem;
        }
        max-width: 20rem;
    }
}

main{
    margin: auto;
    padding: 1rem;
}
.iFrameContainer {
    width: 100%;
    display: flex;
    iframe {
        display: block;
        width: fit-content;
        margin: auto;
        width:  560px;
        height: 315px;
    }
}

footer{
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
        font-size: 1.1rem;
    }
}

.CallBack{
    flex: 1 0 100%;
    text-align: center;
}

a.External{
    flex: 1 0 40%;
    margin:20px;
    img {
        display: block;
        float: left;
    }
}



`

export const Landing = (props) =>
    <LandingStyles>
        <header className="landingHeader">
        <h1>We need help raising $1500 in rent by Monday the 4th of November!</h1>
        <h1>Please help us stay near job leads</h1>
        <h1>If you can help by donating, that is super appreciated</h1>
        <h1>We have a number of things for sale and commission, please browse below</h1>
        <section className="missionStatement">
            <h2>What does Team Tana Do?</h2>
            <ul>
            <li>Miniature painting service from single character to whole armies</li>
            <li>Hi-bit pixel art and hi-bit friendly 3d models</li>
            <li>Indie game design and development</li>
            <li>Website design and development</li>
            <li>Physical media artistic works from illustration to sculpted models</li>
            </ul>
            <h2>And we are hard at work on our own games!</h2>
        </section>
        <section className="whosWho">
            <BlogCallout name="aleesa" image="/assets/ShadowboxThumb.png" />
            <div>
                <h2>Who We Are?</h2> 
                <Link to="callToAction"><h1>Team Tana</h1>
                <h3>aka Aleesa and Liz Tana</h3></Link>
            </div>
            <BlogCallout name="liz" image="/assets/LizBlogThumb.jpg" />
        </section>
        </header>
        <main>
        <div className="iFrameContainer"><iframe width="560" height="315" src="https://www.youtube.com/embed/tcRIx9DAWRQ" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
        <iframe frameBorder="0" scrolling="no" marginHeight="0" marginWidth="0" width="854" height="480" src="https://www.youtube.com/embed/sz9JrVUOV6c"></iframe></div>

        <h2>Why is rent so high?</h2>
        <p>We're in a very expensive neighborhood near previous jobs and near quite a few ongoing job leads.</p>
        <h2>How can I help?</h2>
        <p>Direct Donation via PayPal, buying existing works or commissioning new works in our Commission Catalog and in general getting in touch with short turn around work.</p>
        {props.children}
        </main>
        <footer>
        <a className="External" href="https://discord.gg/kSQp9p3"><img src="/assets/Discord-Logo-White.png" alt="Discord" width="80px" height="80px"/>
        <h3>Join us on Discord, we appreciate the company and it's a great place to discuss commissions, freelancing, or just about anything!</h3></a>
        <a className="External" href="https://www.patreon.com/Aleesatana"><img src="/assets/Patreon_Mark_White.png" alt="Patreon" width="80px" height="80px"/>
        <h3>To support our ongoing game publishing efforts, come see our Patreon</h3></a>
        </footer>
    </LandingStyles>