import React, {useState, Fragment} from 'react';
import {BannerBlock} from './BaseBlock';
import {GalleryImg, GalleryItem} from './GalleryImage';
import {GalleryPage} from './GalleryPage';

const InnerGalleryMini = ({srcHint,alt,price,children,hide,toggleHide}) =>
    <GalleryItem hidden={hide} key={srcHint}>
        <h2>{alt}</h2>
        <div className='ImageBox'>
            <img onClick={toggleHide} src={`/assets/${srcHint}Thumb.png`} alt={alt} />
        </div>
        <div className="PriceContainer">
            <h3>${price}</h3>
        </div>
        <div className="Description">
            {children}
        </div>
        <MiniModal {...{toggleHide, hide, srcHint, alt}} />
    </GalleryItem>

const hintToVideo = {
    "Trolls": "Vkq40avpAn8",
    "PinkMercs": "jkmqNRGyq2M",
    "Cryx": "yV9FH7HKdGE"
}


const MiniModal = ({toggleHide, hide, srcHint, alt}) => 
<div onClick={toggleHide} 
className={`Modal 
${hide? "Modal-exit Modal-exit-active":"Modal-enter Modal-enter-active" }`}>
    {hide? <Fragment /> : <iframe width="560" height="315" src={`https://www.youtube.com/embed/${hintToVideo[srcHint]}`} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen="true"></iframe>}
</div>

export const GalleryMini = (props) =>{
    let [hide,setHide] = useState(true);
    const handleHideClick = () => {
        setHide(!hide);
    }
    let combineProps = {...props,hide,toggleHide:handleHideClick}
    return <InnerGalleryMini {...combineProps}/>

}

const NAME = "Miniatures"
export const MinisCallout = () => 
<BannerBlock className="Callout" path={NAME.toLowerCase()} name={NAME} background="/assets/Background1.jpg" photos={[{name:"Miniatures", url:"/assets/CryxThumb.png"}]}>
    <p>Painted Miniatures of various brands and styles</p>
    <p>Most for sale and are good examples of how to gauge comission quality concerns</p>
</BannerBlock>

export const MinisGallery = () => 
<GalleryPage title="Miniatures">
    <GalleryMini srcHint="Trolls" alt="Trolls" price={500}>
        <p>Has video preview!</p>
        <p>A collection of my Trollblood Hordes army, painted to high tabletop</p>
        <p>Many more and complete units available as well as solos etc</p>
    </GalleryMini>
    <GalleryMini srcHint="Cryx" alt="Cryx" price={1100}>
        <p>Has video preview!</p>
        <p>A high tabletop to mid-display quality army.</p>
        <p>Great example of a force to start and stun a new player with!</p>
    </GalleryMini>

    <GalleryMini srcHint="PinkMercs" alt="Pink Mercenaries" price={500}>
        <p>Has video preview!</p>
    </GalleryMini>
    <GalleryImg srcHint="AngryElves" alt="Angry Elves" price={40}>
        <p>Commission Price Per Model</p>
        <p>Highest Tabletop Quality, still a game-focused base but display quality attention to detail, especially on faces</p>
    </GalleryImg>
    <GalleryImg srcHint="Vera" alt="Vera & Worms" price={50}>
        <p>Vera and the Worms</p>
        <p>A good example of mid tabletop quality for an RPG setting</p>
    </GalleryImg>
</GalleryPage>
