import React from 'react';
import {BlogEntry,BlogList} from "../index"

const lizBlog = () => 
<BlogList name="liz">
    <BlogEntry title="Genestealers?!" date="12019-10-29 HE">
    <p>It's that time of year again, a very spiritual, mystic, ghostly, unholy and sugar filled time of year that some of us hold especially dear.</p>

    <p>This time I find myself on a team that is struggling to not be evicted. This is very stressful as I'm sure you are aware.</p>
    <p>Beyond this, I have committed myself to alleviate this concern by getting some part of commissions in, and I work in ~28mm fantasy, steampunk and sci-fi miniatures as my main physical medium.</p>

    <p>Extra special for me, painting minis has long been a very difficult subject for me. One of my abusers taught me how, and I got treated poorly for continuing to enjoy it throughout my teens, and never thought I could paint fast enough for commission work.</p>
    <p>But I did in my 20's! And I've frankly gotten better and faster since. That's what I'm hoping to show with this blog.</p> 
    <p>In particular, this force is a 100% for me, knock it out quick to medium tabletop quality exercise. Something to loosen me up and get me ready to spend the time it takes on our incoming (I do so very much hope and pray) commissions</p> 

    
    <video title="Start of day" width="720" height="480" controls>
    <source src="/assets/Blog12019-10-29-Start.mp4" type="video/mp4" />
    Your browser does not support the video tag.
    </video>

    <p>I'm working on an all fluff no tough core for my Genestealer Cult army centered around the Necromunda setting, with an Escher and Delaque heavy brood-sibling force, a spyerer and archeotech heavy HQ, and we'll see what else after that!</p>

    <p>I stared today with my one spider-them figure basically finished, as pictured in the video above. Trying to fuse the GW studio Escher paint scheme with a Genestealer slant, especially in the skin and lack of natural hair.</p>

    <p>Below you see my progress, a fair bit of the way done on 3 more of my HQ models. Beyond that I got some good cooking done, took care of my household and fuzzy friends, worked on this website, and took care of my health.</p>

    <img src="/assets/Blog12019-10-29-End.jpg" alt="End of day progress, 4 models now painted" />

    <p>I hope you enjoyed your read, and I hope to see you back soon!</p>
    </BlogEntry>
</BlogList>

export default lizBlog;