import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

function capFirst(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}


export const BlogCallout = ({name, image}) => 
<Link to={`/blog/${name}`}>
    <section>
        <header>{capFirst(name)}'s Blog</header>
        <main><img src={image} alt={name} /></main>
    </section>
</Link>

const BlogListStyles = styled.section`
margin: 2rem;

background: linear-gradient(90deg, rgba(242,255,214,.2) 60%, rgba(242,255,214,0) 92%);
min-height: 100vh;

header{
    margin: auto;
    text-align: justify;
}

.blogEntry{
    margin: 1rem;
    main{
        border: 3px double black;
    }

    img, video {
        display: block;
        margin: 2rem auto;
    }
}

header.blogHead{
    text-align: center;
    h1{
        font-size: 3rem;
    }
}

main{
    margin: auto;
    padding: 1rem;
}

footer{
    margin: auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
        font-size: 1.1rem;
    }
}
`


export const BlogList = ({name,children}) => 
<BlogListStyles>
<header>{capFirst(name)}'s Blog</header>
<main>{children}</main>
<footer className="blogFoot">
<Link className="CallBack" to="/commissions"><h1>►Visit at our Commissions Gallery to get a good look at what we do◄</h1></Link>
<a className="External" href="https://www.patreon.com/Aleesatana"><img src="/assets/Patreon_Mark_White.png" alt="Patreon" width="80px" height="80px"/>
<h3>To support our ongoing publishing efforts, come see our Patreon</h3></a>
<a className="External" href="https://discord.gg/kSQp9p3"><img src="/assets/Discord-Logo-White.png" alt="Discord" width="80px" height="80px"/>
<h3>Join us on Discord, we appreciate the company and it's a great place to discuss commissions, freelancing, or just about anything!</h3>
</a>
</footer>
</BlogListStyles>

export const BlogEntry = ({title,date,children}) =>
    <section className="blogEntry">
        <header className="blogHead">
            <h1>{title}</h1>
            <h2>on {date}</h2>
        </header>
        <main className="blogMain">
            {children}
        </main>
    </section>